export default {
    name: 'Dialog',
    props: {
        //类型：alert,confirm
        type: {
            type: String,
            required: false,
            default: 'alert'
        },
        //标题
        title: {
            type: String,
            required: false,
            default: '温馨提示'
        },
        show: {
            type: Boolean,
            required: true,
            default: true
        },
        cancelBtn: {
            type: String,
            required: false,
            default: '取消'
        },
        confirmBtn: {
            type: String,
            required: false,
            default: '确定'
        }
    },
    methods: {
        // cancel() {
        //     this.show = false;
        // },
        // confirm() {
        //     this.show = false;
        // }，
        dispatch(event, eventStr) {
            this.$dispatch(eventStr);
            // this.show = false;
        }
    }
}


