import Icon from './Icon.vue'

/**
 * 组件内部自动关闭
 */
export default {

  name: 'Toast',
  props: {
    show: {
      type: Boolean,
      required: true,
      default: false,
      twoWay: true
    },
    // 类型：暂不提供
    type: {
      type: String
      // default:

    },
    // 持续时间(毫秒)
    duration: {
      type: Number,
      default: 3000
    }
  },
  watch: {
    show(val) {
      if (this._timeout) clearTimeout(this._timeout)
      if (val && !!this.duration) {
        this._timeout = setTimeout(()=> this.show = false, this.duration)
      }
    }
  }
}


